@import "font/iconfont.css";
:global(.anticon) {
  /* :global() 是为了覆盖全局class .anticon 的样式 */
}
:global(.anticon):before {
  /* 优先调用 anticon-user 字体，就是上面图例创建的 anticon-custom，名字对上就行，我自己的程序叫 anticon-user 了 */
  font-family: "anticon-ts-app", "anticon" !important;
  /* 默认样式是这样
        font-family: "anticon" !important;   
    */
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body,
html,
#root,
.app,
.content-wrapper {
  height: 100%;
}
.app {
  overflow: hidden;
}
.ant-menu-submenu-popup .ant-menu-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
#root .ant-menu-inline .ant-menu-item,
#root .ant-menu-inline .ant-menu-submenu-title {
  width: 100%;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  background-color: #fff;
}
/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  background-color: #9fc8ee;
}



